@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@400;700&display=swap);
#flying-crewmates {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 30vw;
	right: 30vw;
	z-index: 1;

	-webkit-animation: floating 7s ease-in-out alternate 910ms infinite, swipe-in 900ms cubic-bezier(0.28, 0.55, 0.565, 1);

	        animation: floating 7s ease-in-out alternate 910ms infinite, swipe-in 900ms cubic-bezier(0.28, 0.55, 0.565, 1);
}
@-webkit-keyframes floating {
	0%,
	100% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(20px);
	}
}
@keyframes floating {
	0%,
	100% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(20px);
	}
}

#flying-crewmates > img {
	image-rendering: crisp-edges;
	pointer-events: all;
	position: absolute;
	top: 0;
	left: 0;
	width: 64px;

	-webkit-animation: rotation 60s linear infinite;

	        animation: rotation 60s linear infinite;
	transition: transform 100ms linear;
}
#flying-crewmates > img:nth-child(even) {
	animation: rotation 60s linear infinite reverse;
}

#flying-crewmates > img:hover {
	transition: transform 100ms linear;
	scale: 1.1;
}

@-webkit-keyframes swipe-in {
	from {
		transform: translateY(-100px);
	}
	to {
		transform: translateY(0);
	}
}

@keyframes swipe-in {
	from {
		transform: translateY(-100px);
	}
	to {
		transform: translateY(0);
	}
}

@-webkit-keyframes rotation {
	from {
		rotate: 0deg;
	}
	to {
		rotate: 360deg;
	}
}

@keyframes rotation {
	from {
		rotate: 0deg;
	}
	to {
		rotate: 360deg;
	}
}

#info-panel {
	pointer-events: none;
	position: absolute;
	top: 28%;
	bottom: 29%;
	left: 0;
	right: 0;
	z-index: 999;
}
#info-panel.active::after {
	z-index: -1;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	content: " ";
	background-color: rgba(0, 0, 0, 0.418);
	-webkit-animation: fade-in 250ms ease-out;
	        animation: fade-in 250ms ease-out;
}

.info-panel-container {
	pointer-events: all;
	border-top-right-radius: 12px;
	border-bottom-right-radius: 12px;
	border: 3px solid black;
	border-left: none;

	margin-right: 30vw;
	background-color: white;
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
#info-panel.active > .info-panel-container {
	-webkit-animation: popup 300ms ease-out;
	        animation: popup 300ms ease-out;
	transform: none;
}
@-webkit-keyframes popup {
	from {
		transform: translateX(-110%);
	}
	to {
		transform: translateX(0);
	}
}
@keyframes popup {
	from {
		transform: translateX(-110%);
	}
	to {
		transform: translateX(0);
	}
}
@-webkit-keyframes popup-mobile {
	from {
		transform: translateY(-110%);
	}
	to {
		transform: translateY(0);
	}
}
@keyframes popup-mobile {
	from {
		transform: translateY(-110%);
	}
	to {
		transform: translateY(0);
	}
}

@-webkit-keyframes fade-in {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes fade-in {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
.info-panel-image-wrapper {
	background-color: red;
	height: 100.2%;
	width: 30vw;
	-webkit-clip-path: polygon(0 0, 100% 0%, 80% 100%, 0% 100%);
	        clip-path: polygon(0 0, 100% 0%, 80% 100%, 0% 100%);
}
.close-button {
	display: inline-block;
	position: absolute;
	top: 0.18em;
	right: 27.5vw;
	color: white;
	font-weight: bolder;
	font-size: 40px;
	height: 36px;
	width: 36px;
	pointer-events: all;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
}
.info-panel-image {
	height: 100%;
	display: flex;
}
.info-panel-image > img {
	-webkit-animation: slidein-bottom 500ms linear;
	        animation: slidein-bottom 500ms linear;

	margin: auto;
	margin-left: 6.2vw;
	height: 75%;
	-webkit-filter: drop-shadow(-3px 2px 2px rgba(0, 0, 0, 0.5));
	        filter: drop-shadow(-3px 2px 2px rgba(0, 0, 0, 0.5));
}

.info-panel-text {
	border-top-right-radius: 12px;
	border-bottom-right-radius: 12px;

	background-color: white;
	width: 90%;
	margin-left: auto;
	margin-right: auto;

	padding: 14px;
	padding-right: 30px;
	margin-bottom: auto;
}
.info-panel-text-container {
	padding: 20px;
	line-height: 32px;
}
.info-panel-text-container > .header {
	font-size: 50px;
}
.info-panel-text-container > .paragraph {
	line-height: 1.33em;
	margin-top: 20px;
	font-size: 18px;
	text-align: start;
}
.info-panel-text-container > .paragraph > span {
	margin-bottom: 0.36em;
	display: inline-block;
}
.info-panel-text-container > .paragraph > span:last-child {
	margin-bottom: 0;
	display: inline-block;
}

@media (max-width: 1200px) {
	.close-button {
		right: 7vw;
	}
	.info-panel-container {
		margin-right: 11vw;
	}
	.info-panel-image > img {
		margin-left: 3.5vw;
	}
}
@media (max-width: 950px) {
	.close-button {
		right: 6vw;
	}
	.info-panel-image > img {
		margin-left: 1vw;
	}
}
@media (max-width: 768px) {
	.info-panel-text {
		border-radius: 0;
		width: 100%;
		padding: 0;
	}
	#info-panel.active > .info-panel-container {
		-webkit-animation: popup-mobile 300ms ease-out;
		        animation: popup-mobile 300ms ease-out;
		transform: none;
	}
	.close-button {
		top: 1.2vh;
		right: 2vw;
	}
	#info-panel {
		top: 0;
		bottom: 0;
	}
	.info-panel-container {
		border-top: none;
		margin-right: 0;
		width: 100%;
		height: 80%;
		border-radius: 0;
		flex-direction: column;
		justify-content: space-between;
	}
	.info-panel-image-wrapper {
		width: 100%;
		height: 40%;
		-webkit-clip-path: none;
		        clip-path: none;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.45);
	}
	.info-panel-image > img {
		margin-left: auto;
	}
}

